import React from "react";
import "../styles/Header.css";
import Container from "./Container";

const styles = {
  headerStyle: {
    background: "brown"
  },
  headingStyle: {
    fontSize: 50
  }
};
function Section() {
  return (
    <header style={styles.headerStyle} className="Section">
      <br></br>
      <Container/> 
    </header>
    
  );
}

export default Section;
